
  
  #waitingbody {
    background-color: #111;
    
    min-height: 100vh;

  }
  .waviy {
    background-color: #111;
    position: absolute;
    justify-content: center;
    align-items: center;
    text-align: center;

    margin-top: 20%;
    margin-left: 30%;
  }
  .waviy span {

    text-align: center;
    justify-content: center;
    align-items: center;
    padding-bottom: 15px;
    display: block;
    font-size: 40px;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    text-transform: uppercase;
    animation: flip 2s infinite;
    animation-delay: calc(.2s * var(--i))
  }
  @keyframes flip {
    0%,80% {
      transform: rotateY(360deg) 
    }
  }
#loadingtext{
    padding-bottom: 30px;

    font-size:18px
